<template>
    <div class="apiManagement-body">
        <div class="apiManagement-above">
            <el-form :inline="true" class="apiManagement-above-body">
                <el-form-item label="设备">
                    <el-select style="width:120px" size="small" v-model="formInline.value" placeholder="无组织voc">
                        <el-option label="无组织voc" value="V130424RTTS001" />
                        <el-option label="有组织voc" value="V130424RTTS002" />
                    </el-select>
                </el-form-item>
                <el-form-item label="时间粒度">
                    <el-select style="width:120px" size="small" v-model="formInline.timeSize" placeholder="实时">
                        <el-option label="实时" value="DataRealTimeHisColl" />
                        <el-option label="分钟" value="DataMinuteHisColl" />
                        <el-option label="小时" value="DataHourHisColl" />
                        <el-option label="天" value="DataDayHisColl" />
                    </el-select>
                </el-form-item>
                <el-form-item v-show="formInline.timeSize != 'DataRealTimeHisColl'" label="数据">
                    <el-select style="width:120px" size="small" v-model="formInline.data" placeholder="平均值">
                        <el-option label="平均值" value="平均值" />
                        <el-option label="最小值" value="最小值" />
                        <el-option label="最大值" value="最大值" />
                    </el-select>
                </el-form-item>
                <el-form-item v-show="formInline.value == 'V130424RTTS001'" label="时间">
                    <el-date-picker style="width:220px" size="small" v-model="formInline.time" type="datetimerange"
                        range-separator="至" start-placeholder="开始时间" end-placeholder="结束时间"
                        value-format="YYYY-MM-DD HH:mm:ss" />
                </el-form-item>
                <el-form-item>
                    <el-button @click="search($event), open($event)" type="primary" :plain="true">查询</el-button>
                </el-form-item>
            </el-form>
        </div>
        <div class="apiManagement-under">
            <div class="apiManagement-under-body">
                <el-table :data="vocDate.vocDate1" height="450" style="width: 100%" max-height="50%"
                    class="apiManagement-under-body-table">
                    <el-table-column prop="datatime" label="时间" width="500" />
                    <el-table-column prop="sign" label="设备名" width="500" />
                    <el-table-column prop="ppm">
                        <template #header>
                            <div>
                                <div v-if="formInline.value == 'V130424RTTS001'">无组织voc</div>
                                <div v-if="formInline.value == 'V130424RTTS002'">有组织voc</div>
                                <div>(ppm)</div>
                            </div>
                        </template>
                    </el-table-column>>
                </el-table>
                <!-- 分页 -->
                <div class=" apiManagement-under-body-pagination">
                    <el-pagination v-model:current-page="currPage1" v-model:page-size="pageSize1"
                        :page-sizes="[10, 20, 30, 40]" :small="small" :disabled="disabled" :background="background"
                        layout="total, sizes, prev, pager, next, jumper" :total="formInline.total" @size-change="search"
                        @current-change="search" />
                </div>
            </div>
        </div>
    </div>
</template>
  
<script setup>
import { getUnOrgVOCData } from "@/api/VOC";
import { ref, reactive, onMounted } from "vue";
import { ElMessage } from 'element-plus'

const formInline = reactive({
    time: ['', ''],
    timeSize: 'DataRealTimeHisColl',
    data: '平均值',
    value: "V130424RTTS001",
    total: ""
})

//let定义后续使用加.value
let currPage1 = ref(1)
let pageSize1 = ref(10)

function open() {
    ElMessage({
        message: '查询成功',
        type: 'success',
    })
}

function search() {
    let payload = {
        currPage: currPage1.value,
        pageSize: pageSize1.value,
        collName: formInline.timeSize,//时间粒度
        deviceCode: formInline.value,//无组织/有
        startTime: formInline.time[0],
        endTime: formInline.time[1],
    }
    getVocData(payload);

}

let vocDate = reactive({
    vocDate1: [],
})
let dataGet = null
async function getVocData(payload) {
    await getUnOrgVOCData(payload).then((res) => {
        vocDate.vocDate1 = []
        console.log(res);
        dataGet = res.data.data.dataSet
        console.log(23456, dataGet);
        formInline.total = res.data.data.total
        if (formInline.timeSize != 'DataRealTimeHisColl') {
            for (let x = 0; x < dataGet.length; x++) {
                let linshi = {}
                linshi["datatime"] = dataGet[x].dataTime
                if (formInline.value == "V130424RTTS001") {
                    linshi["sign"] = "无组织VOC"
                    linshi["ppm"] = dataGet[x].data[formInline.data]['无组织VOC']
                    console.log(linshi);
                } else {
                    linshi["sign"] = "有组织VOC"
                    linshi["ppm"] = dataGet[x].data[formInline.data]['有组织VOC']
                    console.log(linshi);
                }
                vocDate.vocDate1.push(linshi)
            }
        } else {
            for (let x = 0; x < dataGet.length; x++) {
                let linshi = {}
                linshi["datatime"] = dataGet[x].dataTime
                if (formInline.value == "V130424RTTS001") {
                    linshi["sign"] = "无组织VOC"
                    linshi["ppm"] = '-'
                    console.log(linshi);
                } else {
                    linshi["sign"] = "有组织VOC"
                    linshi["ppm"] = dataGet[x].data['有组织VOC']
                    console.log(linshi);
                }
                vocDate.vocDate1.push(linshi)
            }
        }
    });
}
async function gaibian() {
    console.log(11111111111111);
    vocDate.vocDate1 = []
    if (formInline.timeSize != 'DataRealTimeHisColl') {
        for (let x = 0; x < dataGet.length; x++) {
            let linshi = {}
            linshi["datatime"] = dataGet[x].dataTime
            if (formInline.value == "V130424RTTS001") {
                linshi["sign"] = "无组织VOC"
                linshi["ppm"] = dataGet[x].data[formInline.data]['无组织VOC']
                console.log(linshi);
            } else {
                linshi["sign"] = "有组织VOC"
                linshi["ppm"] = dataGet[x].data[formInline.data]['有组织VOC']
                console.log(linshi);
            }
            vocDate.vocDate1.push(linshi)
        }
    } else {
        for (let x = 0; x < dataGet.length; x++) {
            let linshi = {}
            linshi["datatime"] = dataGet[x].dataTime
            if (formInline.value == "V130424RTTS001") {
                linshi["sign"] = "无组织VOC"
                linshi["ppm"] = '-'
                console.log(linshi);
            } else {
                linshi["sign"] = "有组织VOC"
                linshi["ppm"] = dataGet[x].data['有组织VOC']
                console.log(linshi);
            }
            vocDate.vocDate1.push(linshi)
        }
    }
}
//进入显示
onMounted(() => {
    search()
})     
</script>
  
<style scoped>
.apiManagement-body {
    width: 100%;
    height: 100%;
}

.apiManagement-above {
    position: absolute;
    width: 85%;
    height: 13%;
    background-color: white;
    margin-top: -2%;
}

.apiManagement-above-body {
    font-size: 5px;
    padding: 3%;
    margin-left: -2%;
    display: flex;

}



.apiManagement-above-body-select {
    margin-left: 0.5%;
    margin-top: -0.3%;
    width: 200px;
}

.apiManagement-above-body-button {
    margin-left: 1%;
    margin-top: -0.3%;
}

.apiManagement-under {
    position: absolute;
    background-color: white;
    width: 85%;
    height: 70%;
    margin-top: 5%;
}

.apiManagement-under-body {
    display: flex;
    padding: 1%;

}



.apiManagement-under-body-table {
    margin-left: 0%;

    width: 100%;
    height: 80%;
    overflow: hidden;
}

.apiManagement-under-body-pagination {
    display: inline;
    margin-top: 35%;
    margin-left: -65%;
}
</style>